body {
  background-color: #d0d0d0;
}

#form {
  background-color: #5d5d5d3f;
  border-radius: 30px;
  flex-direction: column;
  gap: 20px;
  width: 768px;
  margin: auto;
  display: flex;
}

#name {
  color: #555;
  text-align: center;
  background-color: #0000;
  border: none;
  padding-top: 20px;
  font-size: 30px;
}

#lastname, #phone, #mail, #submit {
  color: #555;
  text-align: center;
  background-color: #0000;
  border: none;
  height: 50px;
  font-size: 30px;
}

#del {
  color: #555;
  text-align: center;
  background-color: #0000;
  border: none;
  height: 50px;
  padding-bottom: 20px;
  font-size: 30px;
}

input:focus {
  outline: none;
}

input:-webkit-autofill {
  -webkit-border-radius: 30px;
  -webkit-text-fill-color: #555 !important;
  box-shadow: inset 0 0 0 1000px #b6b5b5 !important;
}
